.newChat{
    width: 35%;
    max-width: 415px;
    position: fixed;
    left: 0;
    top: 0;
    bottom:0;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    border-right: 1px solid #ddd;
    transition: all ease 0.5s;
}
.newChat-head{
    display: flex;
    background-color: #00bfa5;
    align-items: center;
    padding: 20px 15px 15px;
}
.newChat-backbtn{
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.newChat-headtitle{
    font-size: 19px;
    height: 40px;
    display: flex;
    align-items: center;
    flex: 1;
    font-weight: bold;
    color: white;
    margin-left: 20px;
}
.newChat-list{
    flex: 1;
    overflow-y: auto;
}
.newChat-list::-webkit-scrollbar{
    width: 6px;
    height: 6px;
}
.newChat-list::-webkit-scrollbar-thumb{
    background-color: rgba(0,0,0,0.2);
}
.newChat-item{
    display: flex;
    align-items: center;
    padding: 15px;
    cursor: pointer;
}
.newChat-item:hover{
    background-color: #f5f5f5;
}
.newChat-itemavatar{
    width:50px;
    height:50px;
    border-radius:50%;
    margin-right: 15px;
}
.newChat-itemname{
    font-size:17px;
    color: black;
}